import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";

@Injectable({
  providedIn: "root"
})
export class RestApiService {
  url2 =
    "http://ec2-3-16-163-232.us-east-2.compute.amazonaws.com:3000/entreprise";
  url = "http://ec2-3-16-163-232.us-east-2.compute.amazonaws.com:3000/users";
  //url = "http://localhost:3000/users";
  //url2 = "http://localhost:3000/entreprise";
  constructor(private http: HttpClient) {}

  Adduser(user) {
    return this.http.post(this.url, user);
  }

  Getuser() {
    return this.http.get(this.url);
  }

  Addentreprise(entreprise) {
    return this.http.post(this.url2, entreprise);
  }
  Removeuser(id) {
    return this.http.delete(this.url + `/${id}`);
  }
}
