import {
  Component,
  ViewChildren,
  ElementRef,
  QueryList,
  OnInit,
  ViewChild
} from "@angular/core";
import { RestApiService } from "../rest-api.service";
import { ToastController } from "@ionic/angular";
import { Socket } from "ngx-socket-io";
import { User } from "../model/users";
import {
  Validators,
  FormBuilder,
  FormGroup,
  ReactiveFormsModule,
  FormControl
} from "@angular/forms";
import { AgeValidator } from "../validator/ageval";
import { UsernameValidator } from "../validator/username";
import { HttpClient } from "@angular/common/http";
@Component({
  selector: "app-home",
  templateUrl: "home.page.html",
  styleUrls: ["home.page.scss"]
})
export class HomePage implements OnInit {
  @ViewChild("signupSlider", { read: ElementRef, static: false })
  signupSlider: ElementRef;
  //@ViewChild("phoneForm", { read: ElementRef, static: false }) phoneForm: ElementRef
  // @ViewChildren("signupSlider") signupSlider1: ElementRef;
  @ViewChildren("itemlist", { read: ElementRef })
  items: QueryList<ElementRef>;
  @ViewChildren("itemlist") elt: ElementRef;
  //private signupSlider: ElementRef;

  public slideOneForm: FormGroup;
  public slideTwoForm: FormGroup;
  public submitAttempt: boolean = false;
  // user = {};
  user: FormGroup;
  ok = false;
  ok2 = false;
  entreprise = {};
  utilisateur = true;
  boulot = false;
  listuser: Array<any>;
  value: string;
  tabtest = [];
  myForm: any;
  pays: any;
  country: any;
  countryflag: any;
  lepays: any;
  // user= {)

  constructor(
    private rest: RestApiService,
    public toastController: ToastController,
    private socket: Socket,
    private formBuilder: FormBuilder,
    private http: HttpClient
  ) {
    // this.presentToaste2();
    this.socket.connect();
    this.Takeuser();
    this.ChangeDate();
    this.removeAnimation();
    this.Initform1();
    this.TakeCountry();
  }

  ngOnInit() {
    this.socket.connect();
    this.Lasocket();
  }

  TakeCountry() {
    this.http
      .get("https://restcountries.eu/rest/v2/region/africa")
      .subscribe(data => {
        console.log(data);
        this.pays = data;
        this.lepays = this.pays[7];
        this.countryflag = this.pays[7]["flag"];
      });
  }
  takecountryflag() {
    console.log(this.country);
    console.log(this.pays[this.country]);
    this.countryflag = this.pays[this.country]["flag"];
    this.lepays = this.pays[this.country];
  }
  next() {
    this.signupSlider.nativeElement.slideNext();
  }

  prev() {
    this.signupSlider.nativeElement.slidePrev();
  }

  save() {
    this.submitAttempt = true;

    if (!this.slideOneForm.valid) {
      // this.signupSlider.nativeElement.slideTo(0);
      console.log("echec desolé!");
    } else {
      this.submitAttempt = false;
      if (this.lepays) {
        this.slideOneForm.value.country = this.lepays["name"];
      }

      this.rest.Adduser(this.slideOneForm.value).subscribe(
        data => {
          // console.log(data);
          this.slideOneForm.reset();
          this.Takeuser();
          this.presentToaste();
        },
        (error: any) => {
          console.log(error);
        }
      );
    }
  }
  ChangeDate() {
    setInterval(() => {
      this.value = new Date().toISOString();
    }, 1000);
  }

  logForm() {
    /*  this.socket.on("newuser", data => {
      console.log(data);
      this.listuser.unshift(data);
    }); */
    this.rest.Adduser(this.user).subscribe(
      data => {
        console.log(data);
        // this.user = {};
        this.Takeuser();
        this.presentToaste();
      },
      (error: any) => {
        console.log(error);
      }
    );
  }

  log2Form() {
    // console.log(this.user);
    this.rest.Addentreprise(this.entreprise).subscribe(
      data => {
        console.log(data);
      },
      (error: any) => {
        console.log(error);
      }
    );
  }

  Takeuser() {
    this.rest.Getuser().subscribe(data => {
      console.log(data);
      this.listuser = data["users"];
      this.tabtest = this.listuser;
      // let tab = data["users"];
      // this.listuser = tab.reverse();
    });
  }

  animateItems() {
    setTimeout(() => {
      let elements = this.items.toArray();
      elements.map(elem => {
        return elem.nativeElement.classList.add("fadeInLeft");
      });
    }, 100);
  }
  segmentButtonClicked(event) {
    /* console.log(event);
    if (this.boulot) {
      this.utilisateur = !this.utilisateur;
      this.boulot = !this.boulot;
    } */
    // this.signupSlider.nativeElement.slideNext();
    this.signupSlider.nativeElement.slidePrev();
  }

  segmentButtonClicked2(event) {
    /* console.log(event);
    if (this.utilisateur) {
      this.utilisateur = !this.utilisateur;
      this.boulot = !this.boulot;
    } */
    this.signupSlider.nativeElement.slideNext();
  }

  async presentToaste() {
    const toast = await this.toastController.create({
      header: "Message du serveur",
      message: "utilisateur  enregistré",
      position: "top",
      cssClass: "my-custom-class",
      duration: 1000,

      buttons: [
        {
          side: "start",
          icon: "person",
          text: "docteuremploi",
          handler: () => {
            console.log("Favorite clicked");
          }
        },
        {
          text: "Done",
          role: "cancel",
          handler: () => {
            console.log("Cancel clicked");
          }
        }
      ]
    });
    toast.present();
  }

  async presentToaste2() {
    const toast = await this.toastController.create({
      header: "DOCETUREMPLOI.COM",
      // message: "utilisateur  enregistré",
      position: "top",
      cssClass: "my-presentation",
      duration: 10000,

      buttons: [
        {
          side: "start",
          icon: "star",
          text: "APPLICATION DESKTOP",
          handler: () => {
            console.log("Favorite clicked");
          }
        },
        {
          text: "Done",
          role: "cancel",
          handler: () => {
            console.log("Cancel clicked");
          }
        }
      ]
    });
    toast.present();
  }
  async Deleteuser(c, i) {
    let t = 0.2;
    //delay-{{ i }}s
    //console.log(this.items);
    let elements = this.items.toArray();
    console.log(i);

    console.log(this.listuser);
    await elements[i].nativeElement.classList.remove("fadeInLeft");

    await elements[i].nativeElement.classList.add(`delay-${t}s`);
    await elements[i].nativeElement.classList.add("bounceOutUp");

    this.rest.Removeuser(c._id).subscribe(data => {
      console.log(data);
      setTimeout(() => {
        this.removeAnimation();
        //this.listuser.splice(i, 1);
      }, 3000);
    });
    setTimeout(() => {
      // this.removeAnimation();
      //this.listuser.splice(i, 1);
      this.Takeuser();
    }, 3000);
  }
  Lasocket() {
    this.socket.emit("hello", "hello erico");

    this.socket.on("moi", data => {
      console.log(data);
    });
  }
  removeAnimation() {
    console.log(this.listuser);
    this.listuser = this.tabtest;
    console.log(this.listuser);
  }

  menu() {
    // this.ok2 = false;
    this.ok = !this.ok;
  }

  menu2() {
    this.ok = false;
    this.ok2 = !this.ok2;
  }

  //gestion du formulaire
  Initform1() {
    this.slideOneForm = this.formBuilder.group({
      prenom: [
        "",
        Validators.compose([
          Validators.maxLength(50),
          Validators.minLength(2),
          Validators.pattern("[a-zA-Z ]*"),
          Validators.required
        ])
      ],
      nom: [
        "",
        Validators.compose([
          Validators.maxLength(25),
          Validators.minLength(5),
          Validators.pattern("[a-zA-Z ]*"),
          Validators.required
        ])
      ],
      age: ["", AgeValidator.isValid],
      genre: ["", Validators.required],
      ville: ["", Validators.required],
      email: [
        "",
        Validators.compose([
          Validators.required,
          Validators.pattern(
            "^[a-zA-Z0-9_.+-]+@[a-zA-Z0-9-]+[(.)]+[a-zA-Z]{2,6}$"
          )
        ])
      ],
      telephone: [
        "",
        Validators.compose([
          Validators.pattern("(^[6])([0-9]{2}){4}"),
          Validators.required
        ])
      ],
      // country: ["", Validators.compose([Validators.required])],
      country: [""],
      diplome1: [""],
      diplome2: [""],
      diplome3: [""],
      formation1: [""],
      formation2: [""],
      formation3: [""]
      /*  telephone: ["", require],
      sex: [""],
      email: [""],
      poste: [
        "",
        Validators.compose([
          Validators.maxLength(30),
          Validators.pattern("[a-zA-Z ]*"),
          Validators.required
        ])
      ] */
    });
  }
}
